figure {
    width: 100%; 
    height: 100vh;
    background: #555;
    position: relative;

    .inner {
        width: 1180px;
        height: 100%;
        margin: 0 auto;
        padding: 50px 0px;
    }
}

@media screen and (max-width: $tablet) {
    figure {
        .inner {
            width: 100%; padding: 20px 6vw;
        }
    }
}