.content {

    >figure {
        width: 100%;
        height: 300px;
        background: #ddd;
        transition: 1s;
    }

    >.inner {
        width: 1180px;
        padding-top: 100px;
        margin: 0 auto;
        transform: translateY(200px);
        opacity: 0;
        transition: 1s;

        h1 {
            font: 30px/1 'arial';
            color: #333;
            margin-bottom: 20px;
        }

        >section {
            min-height: 500px;
            padding-bottom: 100px;
        }
    }
    
    &.on {
        >.inner{
            transform: translateY(0px);
            opacity: 1;
        }
    }
}

@media screen and (max-width: $tablet){
    .content {
        >figure {
            height: 200px;
        }

        >.inner {
            width: 100%; 
            padding: 0px 3vw; 
            padding-top: 80px ;
        }
    }
}